































































































import { Component, Vue, Emit } from "vue-property-decorator";
import BaseSpinner from "../../components/UI/BaseSpinner.vue";
import BaseDialog from "../../components/UI/BaseDialog.vue";

@Component({
  components: {
    BaseSpinner,
    BaseDialog,
  },
})
export default class Login extends Vue {
  email = "";
  password = "";
  mode = "login";
  error = "";
  isLoading = false;
  warningMsg="";
  isWarningMsgActive=false;

  async submitForm() {
      this.isWarningMsgActive=false;
       this.warningMsg="";

    if (
      this.email === "" ||
      !this.email.includes("@") ||
      this.password.length < 6
    ) {
       this.isWarningMsgActive=true;
       this.warningMsg="   /  ";
      return;
    }

    this.isLoading = true;

    const actionPayload = {
      email: this.email,
      password: this.password,
    };

    try {
      if (this.mode === "login") {
        await this.$store.dispatch("login/login", actionPayload);
        this.close();
      } else {
        await this.$store.dispatch("signup", actionPayload);
        this.close();
      }
    } catch (err: any) {
       this.isWarningMsgActive=true;
       this.warningMsg="  /   ";
      this.email="";
      this.password="";
      this.error = err.message || "Failed to authenticate, try later.";

    }
    // this.$router.push({ name: "platform" });
    this.isLoading = false;
    
  }
  @Emit("close")
  close() {
    return "close";
  }
  @Emit("register")
  register() {
    return "register";
  }
}
