




























import { Component, Emit, Prop, Vue } from "vue-property-decorator";

@Component({
  components: {},
})
export default class TextField extends Vue {
  @Prop() private lable!: string;
  @Prop() private placeholder!: string;
  @Prop() private fieldType!: string;
  @Prop() private validated!: string;
  @Prop() private readOnly!: boolean;
  @Prop() private needed!: boolean;
  @Prop() private value!: string;
  textValue = "";
  lng = "ltr";
  // uneditable = false;
  important = this.needed;
  get placeHolder() {
    if (isNaN(parseInt(this.placeholder))) return this.placeholder;
    else return this.placeholder;
  }
  get uneditable() {
    return this.readOnly;
  }
  created() {
    // if (this.readOnly === true) {
    //   this.uneditable = true;
    // }
    if (this.value !== null && this.value !== undefined) {
      this.textValue = this.value;
    }
  }
  @Emit("textChanged")
  textChanged(event: any) {
    return this.textValue;
  }
  @Emit("passwordChanged")
  passwordChanged(event: any) {
    return "passwordChanged";
  }
}
