




















































































































import { Component, Vue, Prop, Watch } from "vue-property-decorator";
import Api from "../../Api";

@Component({
  components: {},
})
export default class Comments extends Vue {
  @Prop() private mainColor!: string;
  @Prop() private itemId!: number;
  @Prop() private page!: string;
  comment = "";
  visibleComments = 5;
  isLoading=false;
  loggedUser = this.$store.getters["login/user"];
  comments = [
    {
      id: 0,
      comment: "",
      commentWritter: "",
      commentProfileImage: require("../../assets/img/Profile/blank-profile-picture.png"),
      commentDate: "24/12/2021",
    },
  ];
  get theme() {
    return this.mainColor;
  }
  get api() {
    if (this.page === "Article") {
      return Api.Platform.GetArticleComments;
    } else if (this.page === "Training") {
      return Api.Platform.GetTrainingComments;
    }
    return "";
  }
  get postBody() {
    if (this.page === "Article") {
      return JSON.stringify({
        Article: this.itemId,
        Comment: this.comment,
        Writter_Name:
          this.loggedUser.first_name + " " + this.loggedUser.last_name,
        Date: this.todayDate(),
      });
    } else if (this.page === "Training") {
      return JSON.stringify({
        Training: this.itemId,
        Comment: this.comment,
        Writter_Name:
          this.loggedUser.first_name + " " + this.loggedUser.last_name,
        Date: this.todayDate(),
      });
    }
    return "";
  }
  get showMore() {
    return this.comments.length - this.visibleComments - 1;
  }
  updateShowedComments() {
    if (this.visibleComments < this.comments.length) {
      this.visibleComments += 5;
    }
  }
  get logInStatus() {
    return this.$store.getters["login/isLoggedIn"];
  }
  async created() {
    await this.fetchComments();
  }
  async fetchComments() {
    let id = this.itemId + "";
    await fetch(this.api.replace("{id}", id))
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.comments = [];
        let results = data.data;
        for (const id in results) {
          let publishDate = "";
          if (results[id].Date != null) {
            publishDate = this.dateM(results[id].Date);
          }
          this.comments.push({
            id: results[id].id,
            commentWritter: results[id].Writter_Name,
            commentDate: publishDate,
            commentProfileImage: require("../../assets/img/Profile/blank-profile-picture.png"),
            comment: results[id].Comment,
          });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }
  async PostComment() {
    this.isLoading=true;
    if (this.logInStatus) {
      await fetch(Api.Platform.PostComment, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: this.postBody,
      })
        .then(() => {
          this.comment = "";
          this.isLoading=false;
          this.fetchComments();
        })
        .catch((error) => {
          this.isLoading=false;
          console.log(error);
        });
    } else {
      this.$store.dispatch("revertLogin");
    }
  }

  dateM(date: string) {
    let temp = date.toString().split("T")[0];
    let yyyy = temp.split("-")[0];
    let mm = temp.split("-")[1];
    let dd = temp.split("-")[2];
    return dd + "/" + mm + "/" + yyyy;
  }
  todayDate() {
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, "0");
    var mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
    var yyyy = today.getFullYear();
    return yyyy + "-" + mm + "-" + dd;
  }
  @Watch("itemId")
  onPropertyChanged(value: string, oldValue: string) {
    this.fetchComments();
  }
}
