

















































































































































import { Component, Vue, Prop } from "vue-property-decorator";
import Api from "../../../Api";

@Component({
  components: {},
})
export default class ResourcesContentComponent extends Vue {
  @Prop() private content!: any;
  @Prop() private mainColor!: string;
  imageGallery = [{}];
  currentId = null;
  PDFViewer=false;
  get logInStatus() {
    return this.$store.getters["login/isLoggedIn"];
  }
  get images() {
    this.imageGallery = [];
    console.log(this.selectedResource)
    if (this.selectedResource.Have_Images === true) {
      for (const id in this.selectedResource.Files) {
        this.imageGallery.push({
          id: id,
          src:
            Api.LocalhostImages +
            this.selectedResource.Files[id].directus_files_id,
          thumbnail:
            Api.LocalhostImages +
            this.selectedResource.Files[id].directus_files_id +
            "?fit=cover&width=200&height=200&quality=80",
        });
      }
    }
    return this.imageGallery;
  }
  get file() {
    
    if (
      this.selectedResource.Files_Type === "Pdf" ||
      this.selectedResource.Files_Type === "Video" ||
      this.selectedResource.Files_Type === "Audio"
    ) {
      console.log(Api.LocalhostImages + this.selectedResource.File)
      return Api.LocalhostImages + this.selectedResource.File;
    }
    return null;
  }
  get selectedResource() {
    return this.content;
  }
  get theme() {
    return this.mainColor;
  }
  get backgroundTheme() {
    return this.mainColor + "b8";
  }
  OpenPDFViewer(){
    this.PDFViewer=true;
  }
  closePDFViewer(){
    
     this.PDFViewer=false;
    
  }
  downloadFile() {
    if (this.logInStatus) {
      window.open(
        "" + Api.LocalhostImages + this.selectedResource.File + "?download"
      );
    } else {
      this.$store.dispatch("revertLogin");
    }
  }
  goToLink() {
    if (this.logInStatus) {
      window.open(this.selectedResource.Link);
    } else {
      this.$store.dispatch("revertLogin");
    }
  }
}
