











import { Component, Vue, Prop, Watch } from "vue-property-decorator";
import Api from "../../Api";
//   @change="postRating"
@Component({
  components: {},
  computed: {},
})
export default class Rating extends Vue {
  @Prop() private totatRatings!: number;
  @Prop() private userRatings!: number;
  @Prop() private rateid!: number;
  @Prop() private id!: number;
  @Prop() private parentCall!: string;
  ratings = 0;
  disabled = true;
  get rating() {
    return this.ratings;
  }
  get subjectId() {
    return this.id;
  }
  get logInStatus() {
    return this.$store.getters["login/isLoggedIn"];
  }
  callBody(payload: number) {
    if (this.parentCall == "training") {
      return JSON.stringify({
        rating_amount: payload,
        training: this.subjectId,
      });
    } else if (this.parentCall == "course") {
      return JSON.stringify({
        rating_amount: payload,
        course: this.subjectId,
      });
    }
    return;
  }
  created() {
    if (this.userRatings) {
      this.ratings = this.userRatings;
      this.disabled = false;
      return this.userRatings;
    } else if (this.totatRatings >= 0) {
      this.ratings = this.totatRatings;
      return this.totatRatings;
    }
  }
  postRating(payload: number) {
    if (this.rateid === 0) {
      fetch(Api.Platform.PostRatings, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.$store.getters["login/token"],
        },
        body: this.callBody(payload),
      })
        .then((data) => {
          console.log("hi there its done");
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      fetch(Api.Platform.PatchRatings.replace("{id}", this.rateid.toString()), {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.$store.getters["login/token"],
        },
        body: this.callBody(payload),
      })
        .then((data) => {
          if (this.parentCall == "training") {
            this.$store.dispatch("setTrainingId", {
              id: this.subjectId,
              value: payload,
            });
          } else if (this.parentCall == "course") {
            this.$store.dispatch("setCourseId", {
              id: this.subjectId,
              value: payload,
            });
          }
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }
  @Watch("userRatings")
  userRatingsChange(newValue: number, oldValue: number) {
    this.ratings = newValue;
  }
  @Watch("totatRatings")
  totatRatingsChange(newValue: number, oldValue: number) {
    this.ratings = newValue;
  }
}
