export const getters = {
  user(state: any) {
    return state.user;
  },
  token(state: any) {
    return state.token;
  },
  isAuthenticated(state: any) {
    return !!state.token;
  },
  didAutoLogout(state: any) {
    return state.didAutoLogout;
  },
  isLoggedIn(state: any) {
    return state.loggedIn;
  },
};
