

























































































import { Component, Vue, Prop, Emit, Watch } from "vue-property-decorator";
import Api from "../../../../Api";

@Component({
  components: {},
})
export default class CoursesSideBar extends Vue {
  @Prop() private programId!: number;
  @Prop() private mainColor!: string;
  subColor = ""; 
  selectedIndex = 0;

  async created() {
    window.onpopstate = () => {
      this.selectedCourse(
        this.courses[this.selectedIndex],
        this.selectedIndex,
        true
      );
    };
    await this.fetchCourses();
    const coursesIndex = this.courses.findIndex(
      (item) => item.id == parseInt(this.$route.params.cardTitleId)
    );
    if (window.innerWidth > 768) {
      if (this.$route.params.cardTitleId && coursesIndex != -1) {
        this.selectedCourse(this.courses[coursesIndex], coursesIndex, true);
      } else {
        this.selectedCourse(this.courses[0], 0, true);
      }
    } else if (window.innerWidth <= 768) {
      if (this.$route.params.cardTitleId && coursesIndex != -1) {
        // this.$set(this.articles[articleIndex],'Selected',true);
        this.selectedCourse(this.courses[coursesIndex], coursesIndex, true);
      } else {
        this.$set(this.courses[0], "Selected", true);
        this.selectedCourse(this.courses[0], 0, true);
      }
    }
    let tempString = this.mainColor + "b5";
    this.subColor = tempString;
  }
  get theme() {
    return this.mainColor;
  }
  get subTheme() {
    return this.mainColor + "b5";
  }
  get logInStatus() {
    return this.$store.getters["login/isLoggedIn"];
  }
  get user() {
    return this.$store.getters["login/user"];
  }
  get token() {
    return this.$store.getters["login/token"];
  }
  get apiHeader() {
    if (this.logInStatus) {
      return {
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.token,
      };
    }
    return {
      "Content-Type": "application/json",
      Authorization: "Bearer " + "",
    };
  }
  courses = [
    {
      id: 0,
      status: "",
      sort: "",
      Program: "",
      Title: "",
      Is_AWT: "",
      Card_Brief: "",
      Card_Image: "",
      Title_Image: "",
      Course_Duration: 0,
      Course_Link: "",
      Starting_Date: "",
      Content: "",
      Ratings: {
        id: 0,
        totalRatings: 0,
        userRatings: 0,
      },
      Selected: false,
    },
  ];

  dateM(date: string) {
    let temp = date.toString().split("T")[0];
    let yyyy = temp.split("-")[0];
    let mm = temp.split("-")[1];
    let dd = temp.split("-")[2];
    return dd + "/" + mm + "/" + yyyy;
  }
  calculateRatings(
    ratings: [
      {
        id: number;
        course: number;
        rating_amount: number;
        user_created: string;
      }
    ]
  ) {
    let totalRating = 0;
    let userRating = 0;
    let ratingId = 0;
    for (const id in ratings) {
      totalRating = totalRating + ratings[id].rating_amount;
      if (
        this.user !== null &&
        this.user.id === ratings[id].user_created &&
        ratings[id].course
      ) {
        userRating = ratings[id].rating_amount;
        ratingId = ratings[id].id;
      }
    }
    totalRating = totalRating / ratings.length;
    return {
      id: ratingId,
      totalRatings: totalRating,
      userRatings: userRating,
    };
  }

  async fetchCourses() {
    let id = this.programId + "";
    await fetch(Api.Platform.GetCourses.replace("{id}", id), {
      headers: this.apiHeader,
    })
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.courses = [];
        let results = data.data;
        for (const id in results) {
          let cardImage = results[id].Card_Image;
          let titleImage = results[id].Title_Image;
          let startingDate = "";
          let ratings = {
            id: 0,
            totalRatings: 0,
            userRatings: 0,
          };
          if (results[id].Starting_Date != null) {
            startingDate = this.dateM(results[id].Starting_Date);
          }
          if (results[id].Card_Image != null) {
            cardImage = Api.LocalhostImages + "" + results[id].Card_Image;
          }
          if (results[id].Title_Image != null) {
            titleImage = Api.LocalhostImages + "" + results[id].Title_Image;
          }
          if (results[id].Ratings.length != 0) {
            ratings = this.calculateRatings(results[id].Ratings);
          }
          this.courses.push({
            id: results[id].id,
            status: results[id].status,
            sort: results[id].sort,
            Program: results[id].program,
            Is_AWT: results[id].Is_AWT,
            Title: results[id].Title,
            Card_Brief: results[id].Card_Brief,
            Card_Image: cardImage,
            Title_Image: titleImage,
            Course_Duration: results[id].Course_Duration,
            Course_Link: results[id].Course_Link,
            Starting_Date: " : " + startingDate,
            Content: results[id].Content,
            Ratings: ratings,
            Selected: false,
          });
        }
        if (data != "" && window.innerWidth > 768) {
          this.courses[0].Selected = true;
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }
  @Watch("$store.getters.courseIdStatus")
  onCourseUpdate(
    value: { id: number; value: number },
    oldValue: { id: number; value: number }
  ) {
    var itemIndex = this.courses.findIndex((item) => item.id === value.id);
    this.courses[itemIndex].Ratings.userRatings = value.value;
    // this.articles[itemIndex].Claps.push(value);
  }

  @Emit("selecteditem")
  selectedCourse(item: any, index: number, createdCall?: boolean) {
    this.$route.params.cardTitle =
      this.courses[index].Title.split(" ").join("-");
    this.$route.params.cardTitleId = this.courses[index].id + "";
    document.title = this.$route.params.cardTitle;
    if (!createdCall) {
      history.pushState(
        {},
        "",
        `/platform/${this.$route.params.navBarId}/${
          this.$route.params.navBar
        }/${this.$route.params.sideNavBarId}/${this.$route.params.sideNavBar}/${
          this.courses[index].id
        }/${this.courses[index].Title.split(" ").join("-")}`
      );
    }
    if (this.selectedIndex !== index || window.innerWidth < 768) {
      this.$set(this.courses[this.selectedIndex], "Selected", false);
      this.$set(this.courses[index], "Selected", true);
      this.selectedIndex = index;
    }
    if (createdCall && window.innerWidth <= 768) {
      return null;
    }
    return item;
  }
}
