


















































import { Component, Vue, Prop } from "vue-property-decorator";
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import "swiper/css/swiper.css";
import Api from "../Api";

@Component({
  components: {
    Swiper,
    SwiperSlide,
  },
})
export default class Testimonials extends Vue {
  @Prop() private id!: number;
  @Prop() private pageName!: string;

  swiperOptions = {
    loop: true,
    slidesPerView: 1,
    slidesPerGroup: 1,
    spaceBetween: 200,
    pagination: {
      el: ".swiper-pagination",
      dynamicBullets: true,
      clickable: true,
    },
    autoplay: {
      delay: 3000,
      disableOnInteraction: true,
    },
  };

  testimonials = [
    {
      Name: "",
      Position: "",
      Content: "",
    },
  ];

  async created() {
    await this.fetchTestimonials();
  }

  async fetchTestimonials() {
    let filterBy = "{id}";
    let filter = this.id + "";
    if (filter === "undefined") {
      filterBy = "?filter[Program][_eq]={id}";
      filter = "?filter[Show_in][_contains]=" + this.pageName;
    }
    await fetch(Api.Testimonials.GetAll.replace(filterBy, filter))
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.testimonials = data.data;
      })
      .catch((error) => {
        console.log(error);
      });
  }
}
