














































import { Component, Vue } from "vue-property-decorator";
import PlatformNavBar from "./PlatformNavBar.vue";
import PlatformSideBar from "./PlatformSideBar/PlatformSidebar.vue";
import ArticleContentComponent from "./PlatformMainContentComponents/ArticleContentComponent.vue";
import ResourcesContentComponent from "./PlatformMainContentComponents/ResourcesContentComponent.vue";
import TrainingContentComponent from "./PlatformMainContentComponents/TrainingContentComponent.vue";
import CoursesContentComponent from "./PlatformMainContentComponents/CoursesContentComponent.vue";
import FaqContentComponent from "./PlatformMainContentComponents/FaqContentComponent.vue";
import Api from "../../Api";

@Component({
  components: {
    PlatformNavBar,
    PlatformSideBar,
    ArticleContentComponent,
    ResourcesContentComponent,
    TrainingContentComponent,
    CoursesContentComponent,
    FaqContentComponent,
  },
})
export default class Platform extends Vue {
  sideBarKey = 0;
  mainColor = "#964A3C";
  selectedProgramId = 1;
  selectedItem: any = {};
  imageSub = Api.LocalhostImages;
  contentComponent = "";
  navBarHandler(item: { id: number; theme: string }) {
    this.selectedProgramId = item.id;
    this.mainColor = item.theme;
    this.sideBarKey += 1;
  }
  sideBarHandler(item: any) {
    this.selectedItem = item;
  }
  mainComponentHandler(component: string) {
    this.selectedItem = null;
    this.contentComponent = component;
  }
  removeSelectedItem() {
    this.selectedItem = null;
  }
  get sideBarClass() {
    if (this.selectedItem) {
      return "is-hidden-mobile";
    }
    return "";
  }
}
