











































































import { Component, Vue, Prop } from "vue-property-decorator";
import Comments from "../../../components/Platform/Comments.vue";
import Rating from "../../../components/Platform/Rating.vue";
@Component({
  components: {
    Comments,
    Rating,
  },
})
export default class CourseContentComponent extends Vue {
  @Prop() private content!: any;
  @Prop() private mainColor!: string;

  get logInStatus() {
    return this.$store.getters["login/isLoggedIn"];
  }
  get selectedCourse() {
    return this.content;
  }
  get theme() {
    return this.mainColor;
  }
  get backgroundTheme() {
    return this.mainColor + "b8";
  }
  goToCourse() {
    if (this.logInStatus) {
      if (this.selectedCourse.Course_Link != null) {
        window.open(this.selectedCourse.Course_Link);
      }
    } else {
      this.$store.dispatch("revertLogin");
    }
  }
}
