









































































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import NavBar from "@/views/NavBar.vue";
import PageFooter from "@/views/PageFooter.vue";
import BaseDialog from "./components/UI/BaseDialog.vue";
@Component({
  components: {
    NavBar,
    PageFooter,
    BaseDialog,
  },
})
export default class App extends Vue {
  navBarColor = "";
  isUserLoading = false;
  showNav = false;
    get terms() {
      var data=  this.$store.getters.getTerms;
      data.isTermsModalOpen= this.$store.getters.getTerms.terms != undefined && this.$store.getters.getTerms.terms.length > 0;
    return   data;
  }
  created() {
    this.onRouteChange();
    this.$store.dispatch("login/tryLogin");
  }
  
  removeTerms() {
    this.$store.dispatch("setTerms", {});
  }
  async acceptTerms() {
    this.isUserLoading = true;
    await this.$store.dispatch("login/updateUser", this.terms.projectId);
    this.isUserLoading = false;
    this.removeTerms();
  
  }
  @Watch("$store.state.navBarColor.color")
  onStateChange() {
    this.navBarColor = this.$store.getters["navBarColor/getNavColor"];
  }
  @Watch("$route")
  onRouteChange() {
    if (this.$route.name === "platform") {
      this.navBarColor = this.$store.getters["navBarColor/getNavColor"];
      this.showNav = false;
    } else {
      this.showNav = true;
      this.navBarColor = "black";
    }
  }
}
