



























































































































import { Component, Vue, Prop } from "vue-property-decorator";
import Clap from "../../../components/Platform/Clap.vue";
import Comments from "../../../components/Platform/Comments.vue";
@Component({
  components: {
    Clap,
    Comments,
  },
})
export default class ArticleContentComponent extends Vue {
  @Prop() private content!: any;
  @Prop() private mainColor!: string;
  isLoading=true;
  get selectedArticle() {
  
  this.isLoading=false;
  return this.content;
  
  }
  get theme() {
    return this.mainColor;
  }
  get backgroundTheme() {
    return this.mainColor + "b8";
  }
}
