import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import Home from "../views/Home.vue";
import Platform from "../views/Platform/Platform.vue";

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/AboutUs/AboutUs.vue"),
  },
  {
    path: "/our-team",
    name: "our-team",
    component: () => import("../views/OurTeam/OurTeam.vue"),
  },
  {
    path: "/join-our-team",
    name: "join-our-team",
    component: () => import("../views/OurTeam/JoinOurTeam.vue"),
  },
  {
    path: "/our-programs",
    name: "our-programs",
    component: () => import("../views/OurPrograms/OurPrograms.vue"),
  },
  {
    path: "/our-programs/:programName",
    name: "single-program",
    component: () => import("../views/OurPrograms/SingleProgram.vue"),
  },
  {
    path: "/contact-us",
    name: "contact-us",
    component: () => import("../views/ContactUs/ContactUs.vue"),
  },
  {
    path: "/our-purpose",
    name: "our-purpose",
    component: () => import("../views/OurPurpose/OurPurpose.vue"),
  },
  {
    path: "/login",
    name: "login",
    component: () => import("../views/Authentication/Login.vue"),
  },
  {
    path: "/profile",
    name: "profile",
    component: () => import("../views/User/ProfilePage.vue"),
  },
  {
    path: "/register",
    name: "register",
    component: () => import("../views/User/RegisterUser.vue"),
  },
  // {
  //   path: "/platform",
  //   name: "platform",
  //   component: Platform,
  // },
  // {
  //   path: "/platform/:navBarId?/:navBar?",
  //   name: "platform",
  //   props: true,
  //   // name: "platform-navbar",
  //   component: Platform,
  // },
  {
    path: "/platform/:navBarId?/:navBar?/:sideNavBarId?/:sideNavBar?/:cardTitleId?/:cardTitle?",
    name: "platform",
    props: true,
    component: Platform,
  },
  // {
  //   path: "/platform/:navBarId/:navBar/:sideNavBarId/:sideNavBar/:cardTitleId/:cardTitle",
  //   name: "platform-card",
  //   component: () => import( "../views/Platform/Platform.vue"),
  // },

  {
    path: "/popup",
    name: "popup",
    component: () => import("../views/TestOnly/PopUp.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    }
    return { x: 0, y: 0 };
  },
});

export default router;
