















import { Component, Vue, Emit, Prop , Watch} from "vue-property-decorator";
import Api from "../../Api";

@Component({
  components: {},
})
export default class PlatformNavBar extends Vue {
  @Prop() private mainColor!: string;
  // @Prop() private navBarId!: string;
  tempFix:any;
  swiperOption = {
    slidesPerView: 3.5,
    spaceBetween: 0,
  };
  selectedIndex = 0;
  programs = [
    {
      id: 0,
      Name: "",
      color: "rgba(255, 255, 255, 1)",
      theme: "",
      Access_Level: true,
      Terms_And_Conditions: "",
    },
  ];
  get logInStatus() {
    return this.$store.getters["login/isLoggedIn"];
  }
  get platformNavBarStore() {
    return this.$store.getters.getPlatformNavBar;
  }
  async created() {
    await this.fetchPrograms();
    // console.log(this.navBarId)
    // console.log(this.$route.params.navBarId)
    this.programs[this.selectedIndex].color = "rgba(255, 255, 255, 1)";
  }
  //  await this.$store.dispatch("login/login", actionPayload);
  async fetchPrograms() {
    await fetch(Api.Platform.GetPrograms)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.programs = [];
        for (const id in data.data) {
          this.programs.push({
            id: data.data[id].id,
            Name: data.data[id].Name,
            color: "rgba(255, 255, 255, 0.5)",
            theme: data.data[id].Theme,
            Access_Level: data.data[id].Access_Level,
            Terms_And_Conditions: data.data[id].Terms_And_Conditions,
          });
        }
        if (this.$route.params.navBarId) {
          const navId = parseInt(this.$route.params.navBarId);
          const index = this.programs.findIndex(
            (program) => program.id == navId
          );
          this.selectedIndex = index;
          this.emitHandler(this.programs[index], index, true);
          // alert(id)
        } else {
          for (const id in this.programs) {
            if (this.emitHandler(this.programs[id], parseInt(id), true)) {
              break;
            }
          }
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }

  emitHandler(
    item: { id: number; theme: string },
    index: number,
    selector?: boolean
  ) {
    if (this.programs[index].Access_Level) {
      if (this.logInStatus) {
        let userPrograms = this.$store.getters["login/user"].Programs;
        if (this.$store.getters["login/user"].Programs != null) {
          userPrograms = userPrograms.find((element) => element == item.id);
        }
        if (userPrograms !== undefined && userPrograms !== null) {
          this.selectedId(item, index);
          return true;
        } else {
          if (!selector) {
           
            this.$store.dispatch("setTerms", {
              projectId: item.id,
              terms: this.programs[index].Terms_And_Conditions,
            });
            this.tempFix={item:item,index:index}
          }
        }
      } else {
        if (!selector) this.$store.dispatch("revertLogin");
      }
    } else {
      this.selectedId(item, index);
      return true;
    }
  }
  @Emit("selectedId")
  selectedId(item: { id: number; theme: string }, index: number) {
    let programChildren = this.platformNavBarStore;
    const programChildIndxe = this.platformNavBarStore.findIndex(
      (child) => child.id == item.id
    );
    if (programChildIndxe == -1 && isNaN(programChildren[0].id)) {
      // console.log('no id')
      programChildren = [];
      programChildren.push({
        id: item.id,
        // sidebarHeaderIndex:this.$route.params.sideNavBarId,
      });
      this.$store.dispatch("setPlatformNavBar", programChildren);
    } else if (programChildIndxe == -1 && !isNaN(programChildren[0].id)) {
      // console.log('have others id')
      programChildren.push({
        id: item.id,
        // sidebarHeaderIndex:this.$route.params.sideNavBarId,
      });
      this.$store.dispatch("setPlatformNavBar", programChildren);
    }
    if (!this.$route.params.navBarId && !this.$route.params.navBar) {
      this.$router.replace({
        params: {
          navBarId: this.programs[index].id + "",
          navBar: this.programs[index].Name.split(" ").join("-"),
        },
      });
    }

    if (this.selectedIndex !== index) {
      this.programs[this.selectedIndex].color = "rgba(255, 255, 255, 0.5)";
      this.selectedIndex = index;
      // this.$router.push({params:{navBarId:item.id+'',navBar:this.programs[index].Name.split(" ").join("-")}});
     this.$route.params.navBar = this.programs[index].Name.split(" ").join("-")
     history.pushState(
        {},
        "",
        `/platform/${item.id + ""}/${this.programs[index].Name.split(" ").join(
          "-"
        )}`
      );
    }
    this.$store.dispatch("navBarColor/setNavColor", item.theme);
    this.programs[index].color = "rgba(255, 255, 255, 1)";
  }
  get user(){
    return this.$store.getters['login/user']
  }
  @Watch("user")
    test(){
    this.emitHandler(this.tempFix.item,this.tempFix.index);
    }
}
