<template>
  <div>
    <div class="section-style" dir="rtl">
      <!-- <div class="logo-container">
        <b-image
          class="footer-logo"
          :src="require('@/assets/img/footer-logo.png')"
          :responsive="true"
        ></b-image>
      </div> -->
      <div class="columns">
        <div class="column">
          <div @click="showFixedList" class="dropDown-section">
            <div class="main-item">
              <router-link :to="{name:fixedColumn.mainTextUrl}">{{
                fixedColumn.mainText
              }}</router-link>
            </div>
          </div>
          <div v-if="fixedColumn.opened === true || desktopSize === true">
            <div class="sub-item">{{ fixedColumn.subText }}</div>
            <div
              v-for="(item, index) in fixedColumn.subCategories"
              :key="index"
              class="sub-item-grey"
            >
              {{ item.title }} <br />
              {{ item.data }}
            </div>
          </div>
        </div>
        <div v-for="(column, index) in columns" :key="index" class="column">
          <div @click="showColumnList(index)" class="dropDown-section">
            <div class="main-item">
              <router-link :to="{ name: column.mainUrl }">{{
                column.mainText
              }}</router-link>
            </div>
          </div>
          <div v-if="column.opened === true || desktopSize === true">
            <div
              v-for="(sub, index) in column.subCategories"
              :key="index"
              class="sub-item"
            >
              <router-link :to="{ name: sub.itemUrl }">{{
                sub.itemName
              }}</router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Component, Vue } from "vue-property-decorator";
import Api from "../Api";

@Component({
  components: {},
})
export default class PageFooter extends Vue {
  desktopSize = true;
  fixedColumn = {
    mainText: " ",
    mainTextUrl: "contact-us",
    subText: "  ",
    opened: false,
    subCategories: [
      {
        title: "  :",
        data: ".. 850881  11185 ",
      },
      {
        title: " /  :",
        data: "962 6 5562330",
      },
      {
        title: "  :",
        data: "contact@arabwomantoday.com",
      },
    ],
  };
  columns = [
    {
      mainText: " ",
      mainUrl: "/about",
      subCategories: [
        {
          itemName: "",
          itemUrl: "",
        },
      ],
    },
  ];
  async created() {
    await this.fetchMenu();
    if (window.innerWidth < 769) {
      this.desktopSize = false;
    }
  }
  showFixedList() {
    this.fixedColumn.opened = !this.fixedColumn.opened;
    this.checkWindowSize();
  }
  showColumnList(index) {
    this.columns[index].opened = !this.columns[index].opened;
    this.checkWindowSize();
  }
  checkWindowSize() {
    if (window.innerWidth < 769) {
      this.desktopSize = false;
    }
  }
  async fetchMenu() {
    await fetch(Api.MenuItems.GetFixed)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        let result = data.data;
        this.fixedColumn.mainTextUrl = result.Title_Url;
        if (result.Post_Address !== null) {
          this.fixedColumn.subCategories[0].data = result.Post_Address;
        } else {
          this.fixedColumn.subCategories.splice(0, 1);
        }
        if (result.Phone_Fax !== null) {
          this.fixedColumn.subCategories[1].data = result.Phone_Fax;
        } else {
          this.fixedColumn.subCategories.splice(1, 1);
        }
        if (result.Email !== null) {
          this.fixedColumn.subCategories[2].data = result.Email;
        } else {
          this.fixedColumn.subCategories.splice(2, 1);
        }
      })
      .catch((error) => {
        console.log(error);
      });

    await fetch(Api.MenuItems.GetAll)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.columns.pop();
        let result = data.data;
        let url = "";
        for (const id in result) {
          if (result[id].Item_Url !== null) {
            url = result[id].Item_Url;
          } else {
            url = "";
          }
          this.columns.push({
            mainText: result[id].Name,
            mainUrl: url,
            opened: false,
            subCategories: [],
          });

          for (const subId in result[id].Sub_Menu_Items) {
            if (result[id].Sub_Menu_Items[subId].Item_Url !== null) {
              url = result[id].Sub_Menu_Items[subId].Item_Url;
            } else {
              url = "";
            }
            this.columns[id].subCategories.push({
              itemName: result[id].Sub_Menu_Items[subId].Name,
              itemUrl: url,
            });
          }
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }
}
</script>

<style scoped lang="scss">
.columns {
  // padding: 2.92vw;
  // padding: 0 20px 0 0;
  margin: 0 20px 0 0;
}
.footer-logo {
  width: 16.2vw;
}
.section-style {
  background-color: #2c2c2c;
}
.logo-container {
  padding-top: 3.3vw;
  display: flex;
  justify-content: center;
}

.main-item {
  // margin: 4.5vw 0 1.5vw 0;
  margin: 10px 0 1.5vw 0;
  font-family: Almarai;
  font-size: 1.72vw;
  font-weight: bold;
  text-align: right;
  color: #fff;
}
.main-item ::v-deep a {
  color: #fff;
}
.sub-item {
  margin-bottom: 1.5vw;
  font-family: Almarai;
  font-size: 1.32vw;
  font-weight: bold;
  text-align: right;
  color: #fff;
}
.sub-item ::v-deep a {
  color: #fff;
}
.sub-item-grey {
  margin: 0 0 0.7vw 8vw;
  font-family: Almarai;
  font-size: 1.13vw;
  text-align: right;
  color: #dbdbdb;
}
.dropDown-section {
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 769px) {
  .column {
    padding-top: 0;
  }
  .footer-logo {
    width: 16.2vw;
    width: 200px;
  }
  .main-item {
    margin: 4.5vw 0 1.5vw 0;
    margin: 0 0 1.5vw 0;
    font-size: 28px;
  }
  .sub-item {
    font-size: 18px;
  }
  .sub-item-grey {
    font-size: 18px;
  }
}
</style>
