






















import { Component, Vue, Emit, Prop } from "vue-property-decorator";

@Component({
  components: {},
})
export default class PlatformSideBarHeader extends Vue {
  @Prop() private mainColor!: string;
  transitionClass = "";
  selectedIndex = 0;
  left = true;
  right = false;
  rightArrowTransition = "";
  items = [
    {
      name: "",
      value: "Articles",
      component: "articles-side-bar",
      backgroundColor: "white",
      contentComponent: "article-content-component",
      color: this.mainColor,
    },
    {
      name: "",
      value: "Resources",
      component: "resources-side-bar",
      backgroundColor: "grey",
      contentComponent: "resources-content-component",
      color: "white",
    },
    {
      name: "",
      value: "Trainings",
      component: "training-side-bar",
      backgroundColor: "grey",
      contentComponent: "training-content-component",
      color: "white",
    },
    {
      name: " ",
      value: "Courses",
      component: "courses-side-bar",
      backgroundColor: "grey",
      contentComponent: "courses-content-component",
      color: "white",
    },
    {
      name: " ",
      value: "FAQ",
      component: "faq-side-bar",
      backgroundColor: "grey",
      contentComponent: "faq-content-component",
      color: "white",
    },
  ];
  get platformNavBarStore() {
    return this.$store.getters.getPlatformNavBar;
  }
  created() {
    let abc = this.platformNavBarStore.findIndex(
      (item) => item.id == this.$route.params.navBarId
    );
    if (abc != -1 && this.platformNavBarStore[abc].sidebarHeaderIndex >= 0) {
      const navBarId = this.platformNavBarStore[abc].sidebarHeaderIndex;
      // console.log('created store header')
      this.selectedHeader(this.items[navBarId], navBarId);
    } else if (this.$route.params.sideNavBarId) {
      // console.log('created params header')
      const navBarId = parseInt(this.$route.params.sideNavBarId);
      this.selectedHeader(this.items[navBarId], navBarId);
    } else {
      // console.log('created else')
      this.selectedHeader(this.items[0], 0);
    }
    if (!this.$route.params.sideNavBarId && !this.$route.params.sideNavBar) {
      this.$router.replace({
        params: {
          sideNavBarId: "0",
          sideNavBar: this.items[0].name.split(" ").join("-"),
        },
      });
    }
  }
  moveLeft() {
    this.transitionClass = "move-right";
    this.left = false;
    this.right = true;
    this.rightArrowTransition = "right-arrow-transition";
  }
  moveRight() {
    this.transitionClass = "move-left";
    this.right = false;
    this.left = true;
    this.rightArrowTransition = "";
  }
  emitHandler(
    item: {
      name: string;
      value: string;
      component: string;
      contentComponent: string;
    },
    index: number
  ) {
    if (this.selectedIndex !== index) {
      this.selectedHeader(item, index);
    }
  }

  @Emit("selectedHeader")
  selectedHeader(
    item: {
      name: string;
      value: string;
      component: string;
      contentComponent: string;
    },
    index: number
  ) {
    if (this.selectedIndex !== index) {
      this.items[this.selectedIndex].backgroundColor = "grey";
      this.items[this.selectedIndex].color = "white";
      this.selectedIndex = index;
      if (index > 2) {
        this.moveLeft();
      }
      if (this.$route.params.sideNavBarId !== index + "") {
        // let storeNavBarIndex = this.platformNavBarStore.findIndex(item =>item.id == this.$route.params.navBarId)
        // if(storeNavBarIndex != -1){
        //   this.$store.state.platformNavBar[storeNavBarIndex].sidebarHeaderIndex = index
        // }

        // this.$router.push({
        //   params: {
        //     sideNavBarId: index + "",
        //     sideNavBar: this.items[index].name,
        //   },
        // });

        history.pushState(
          {},
          "",
          `/platform/${this.$route.params.navBarId}/${
            this.$route.params.navBar
          }/${index + ""}/${this.items[index].name}`
        );
      }
    }
    this.items[index].backgroundColor = "white";
    this.items[index].color = this.mainColor;
    // return item;
  }
}
