




















import { Component, Vue, Prop, Watch } from "vue-property-decorator";
import Api from "../../Api";

@Component({
  components: {},
})
export default class Clap extends Vue {
  @Prop() private claps!: number;
  @Prop() private articleId!: number;
  @Prop() private mainColor!: number;
  @Prop() private userClaped!: number[];
  isBtnLoading = false;

  get theme() {
    return this.mainColor;
  }
  get logInStatus() {
    return this.$store.getters["login/isLoggedIn"];
  }
  get userDidClaped() {
    return this.userClaped.length > 0;
  }

  postClap() {
    this.isBtnLoading = true;
    if (this.logInStatus && this.userClaped.length == 0) {
      
      fetch(Api.Platform.PostClap.replace("{id}", this.articleId.toString()), {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.$store.getters["login/token"],
        },
        body: JSON.stringify({
          Clap: this.claps + 1,
        }),
      })
        .then((data) => {
          this.$store.dispatch("setArticleId", this.articleId);
          this.isBtnLoading = false;
        })
        .catch((error) => {
          console.log(error);
          this.isBtnLoading = false;
        });
      fetch(Api.Platform.PostClaps, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + this.$store.getters["login/token"],
        },
        body: JSON.stringify({
          article_claps: this.articleId,
          user_clap: this.articleId,
        }),
      }).catch((error) => {
        console.log(error);
        this.isBtnLoading = false;
      });
    } else if (!this.logInStatus) {
      this.$store.dispatch("revertLogin");
      this.isBtnLoading = false;
    }
    
   
  }
}
