













































































































































import { Component, Vue, Prop, Watch, Emit } from "vue-property-decorator";
import Api from "../../../../Api";
@Component({
  components: {},
})
export default class ArticlesSideBar extends Vue {
  @Prop() private programId!: number;
  @Prop() private mainColor!: string;
  subColor = "";
  selectedIndex = 0;
  articles = [
    {
      id: 0,
      Status: "",
      Program: "",
      Title: "",
      Card_Image: "",
      Brief: "",
      Writer_Name: "",
      Publish_Date: "",
      Title_Image: "",
      Is_AWT: "",
      Is_Common: "",
      Content: "",
      Clap: "",
      Claps: [0],
      Selected: false,
    },
  ];
  get logInStatus() {
    return this.$store.getters["login/isLoggedIn"];
  }
  get header() {
    if (this.logInStatus) {
      return {
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.$store.getters["login/token"],
      };
    } else {
      return { "Content-Type": "application/json", Authorization: "" };
    }
  }
  test() {
    console.log("hi there");
    return null;
  }
  async created() {
    window.onpopstate = () => {
      this.selectedArticle(
        this.articles[this.selectedIndex],
        this.selectedIndex,
        true
      );
    };
    await this.fetchArticles();
    const articleIndex = this.articles.findIndex(
      (item) => item.id == parseInt(this.$route.params.cardTitleId)
    );
    if (window.innerWidth > 768) {
      if (this.$route.params.cardTitleId && articleIndex != -1) {
        this.selectedArticle(this.articles[articleIndex], articleIndex, true);
      } else {
        this.selectedArticle(this.articles[0], 0, true);
      }
    } else if (window.innerWidth <= 768) {
      if (this.$route.params.cardTitleId && articleIndex != -1) {
        // this.$set(this.articles[articleIndex],'Selected',true);
        this.selectedArticle(this.articles[articleIndex], articleIndex, true);
      } else {
        this.$set(this.articles[0], "Selected", true);
        this.selectedArticle(this.articles[0], 0, true);
      }
    }

    let tempString = this.mainColor + "b5";
    this.subColor = tempString;
  }
  dateM(date: string) {
    let temp = date.toString().split("T")[0];
    let yyyy = temp.split("-")[0];
    let mm = temp.split("-")[1];
    let dd = temp.split("-")[2];
    return dd + "/" + mm + "/" + yyyy;
  }

  async fetchArticles() {
    let id = this.programId + "";
    await fetch(Api.Platform.GetArticle.replace("{id}", id), {
      method: "GET",
      headers: this.header,
    })
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.articles = [];
        let results = data.data;
        for (const id in results) {
          let titleImage = results[id].Title_Image;
          let cardImage = results[id].Card_Image;
          let publishDate = "";
          if (results[id].Publish_Date != null) {
            publishDate = this.dateM(results[id].Publish_Date);
          }
          if (results[id].Title_Image != null) {
            titleImage = Api.LocalhostImages + "" + results[id].Title_Image;
          }
          if (results[id].Card_Image != null) {
            cardImage = Api.LocalhostImages + "" + results[id].Card_Image;
          }
          this.articles.push({
            id: results[id].id,
            Status: results[id].Status,
            Program: results[id].program,
            Title: results[id].Title,
            Card_Image: cardImage,
            Brief: results[id].Brief,
            Writer_Name: results[id].Writer_Name,
            Publish_Date: publishDate,
            Clap: results[id].Clap,
            Title_Image: titleImage,
            Content: results[id].Content,
            Is_AWT: results[id].Is_AWT,
            Is_Common: results[id].Is_Common,
            Claps: results[id].Claps,
            Selected: false,
          });
        }
        if (data != "" && window.innerWidth > 768) {
          this.articles[0].Selected = true;
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }
  @Watch("$store.getters.articleIdStatus")
  onArticleUpdate(value: number, oldValue: number) {
    var itemIndex = this.articles.findIndex((item) => item.id === value);
    this.articles[itemIndex].Clap = this.articles[itemIndex].Clap + 1;
    this.articles[itemIndex].Claps.push(value);
  }

  @Watch("programId")
  onPropertyChanged(value: string, oldValue: string) {
    this.fetchArticles();
  }

  @Emit("selecteditem")
  selectedArticle(item: any, index: number, createdCall?: boolean) {
    if (!this.$route.params.cardTitleId && !this.$route.params.cardTitle) {
      this.$router.replace({
        params: {
          cardTitleId: this.articles[0].id + "",
          cardTitle: this.articles[0].Title.split(" ").join("-"),
        },
      });
    }
    if (this.selectedIndex !== index || window.innerWidth < 768) {
      this.$route.params.cardTitle =
        this.articles[index].Title.split(" ").join("-");
      this.$route.params.cardTitleId = this.articles[index].id + "";
      document.title = this.$route.params.cardTitle;
      if (!createdCall) {
        history.pushState(
          {},
          "",
          `/platform/${this.$route.params.navBarId}/${
            this.$route.params.navBar
          }/${this.$route.params.sideNavBarId}/${
            this.$route.params.sideNavBar
          }/${this.articles[index].id}/${this.articles[index].Title.split(
            " "
          ).join("-")}`
        );
      }
      this.$set(this.articles[this.selectedIndex], "Selected", false);
      this.$set(this.articles[index], "Selected", true);
      this.selectedIndex = index;
    }
    if (createdCall && window.innerWidth <= 768) {
      return null;
    }
    return item;
  }
}
