import Vue from "vue";
import Vuex from "vuex";
import { Login } from "./auth/index";

Vue.use(Vuex);
const navBarColor = {
  namespaced: true,
  state: () => ({
    color: "",
  }),
  mutations: {
    setNavColorState(state: any, payload: any) {
      state.color = payload;
    },
  },
  actions: {
    setNavColor(context: any, payload: any) {
      context.commit("setNavColorState", payload);
    },
  },
  getters: {
    getNavColor(state: any) {
      return state.color;
    },
  },
};

export default new Vuex.Store({
  state: {
    loginPop: false,
    articleId: Number,
    platformNavBar: [
      {
        id: Number,
        sidebarHeaderIndex: Number,
      },
    ],
    trainingId: {
      id: Number,
      value: Number,
    },
    courseId: {
      id: Number,
      value: Number,
    },
    terms: {
      projectId: 0,
      terms: "",
    },
  },
  mutations: {
    revertLoginPop(state: any) {
      state.loginPop = !state.loginPop;
    },
    changeArticleId(state: any, payload: any) {
      state.articleId = payload;
    },
    changeTrainingId(state: any, payload: any) {
      state.trainingId = payload;
    },
    changeCourseId(state: any, payload: any) {
      state.courseId = payload;
    },
    setTermsState(state: any, payload: any) {
      state.terms.projectId = payload.projectId;
      state.terms.terms = payload.terms;
    },
    changePlatformNavBar(state: any, payload: any) {
      state.platformNavBar = payload;
    },
  },
  actions: {
    revertLogin(context: any, payload: any) {
      context.commit("revertLoginPop");
    },
    setArticleId(context: any, payload: any) {
      context.commit("changeArticleId", payload);
    },
    setTrainingId(context: any, payload: any) {
      context.commit("changeTrainingId", payload);
    },
    setCourseId(context: any, payload: any) {
      context.commit("changeCourseId", payload);
    },
    setTerms(context: any, payload: any) {
      context.commit("setTermsState", payload);
    },
    setPlatformNavBar(context: any, payload: any) {
      context.commit("changePlatformNavBar", payload);
    },
  },
  getters: {
    loginPopStatus(state: any) {
      return state.loginPop;
    },
    articleIdStatus(state: any) {
      return state.articleId;
    },
    trainingIdStatus(state: any) {
      return state.trainingId;
    },
    courseIdStatus(state: any) {
      return state.courseId;
    },
    getTerms(state: any) {
      return state.terms;
    },
    getPlatformNavBar(state: any) {
      return state.platformNavBar;
    },
  },
  modules: {
    navBarColor: navBarColor,
    login: Login,
  },
});
