

















































































































































































import { Component, Vue, Emit } from "vue-property-decorator";
import TextField from "../../components/common/TextField.vue";
import BaseSpinner from "../../components/UI/BaseSpinner.vue";
import BaseDialog from "../../components/UI/BaseDialog.vue";
import Api from "../../Api";

@Component({
  components: {
    TextField,
    BaseDialog,
    BaseSpinner,
  },
})
export default class RegisterUser extends Vue {
  //   loggedUser = this.$store.getters["login/user"];
  //   accessToken = this.$store.getters["login/token"];
  isLoading = false;
  accountCreated = false;
  error = "";
  userDetails = {
    first_name: "",
    f_n_v: "",
    last_name: "",
    l_n_v: "",
    email: "",
    e_v: "",
    password: "",
    p_v: "",
    confirmPassword: "",
    c_p_v: "",
    avatar: "",
    title: "",
  };

  test() {
    alert(this.userDetails);
  }
  validateForm() {
    let validated = true;
    if (this.userDetails.first_name === "") {
      this.userDetails.f_n_v = "validated";
      validated = false;
    } else {
      this.userDetails.f_n_v = "";
    }
    if (this.userDetails.last_name === "") {
      this.userDetails.l_n_v = "validated";
      validated = false;
    } else {
      this.userDetails.l_n_v = "";
    }
    if (
      !this.userDetails.email.includes("@") ||
      this.userDetails.email === ""
    ) {
      this.userDetails.e_v = "validated";
      validated = false;
    } else {
      this.userDetails.e_v = "";
    }
    if (this.userDetails.password === "") {
      this.userDetails.p_v = "validated";
      validated = false;
    } else {
      this.userDetails.p_v = "";
    }
    if (
      this.userDetails.confirmPassword === "" ||
      this.userDetails.confirmPassword !== this.userDetails.password
    ) {
      this.userDetails.c_p_v = "validated";
      validated = false;
    } else {
      this.userDetails.c_p_v = "";
    }
    if (validated) {
      this.submitForm();
    }
  }
  async submitForm() {
    if (
      this.userDetails.email === "" ||
      this.userDetails.first_name === "" ||
      this.userDetails.last_name === "" ||
      !this.userDetails.email.includes("@") ||
      this.userDetails.password.length < 6
    ) {
      return;
    }

    this.isLoading = true;

    const actionPayload = {
      email: this.userDetails.email,
      password: this.userDetails.password,
    };

    try {
      await this.$store.dispatch("login/signup", actionPayload);
    } catch (err: any) {
      this.error = err.message || "Failed to authenticate, try later.";
    }
    this.isLoading = false;
    this.accountCreated = true;
    setTimeout(() => {
      this.accountCreated = false;
      this.close();
    }, 1000);
    // this.close();
  }

  @Emit("close")
  close() {
    return "close";
  }
}
