

























































































import { Component, Vue, Prop, Emit, Watch } from "vue-property-decorator";
import Api from "../../../../Api";

@Component({
  components: {},
})
export default class TrainingSideBar extends Vue {
  @Prop() private programId!: number;
  @Prop() private mainColor!: string;
  subColor = "";
  selectedIndex = 0;

  async created() {
    window.onpopstate = () => {
      this.selectedTraining(
        this.trainings[this.selectedIndex],
        this.selectedIndex,
        true
      );
    };
    await this.fetchTrainings();
    const trainingIndex = this.trainings.findIndex(
      (item) => item.id == parseInt(this.$route.params.cardTitleId)
    );
    if (window.innerWidth > 768) {
      if (this.$route.params.cardTitleId && trainingIndex != -1) {
        this.selectedTraining(this.trainings[trainingIndex], trainingIndex, true);
      } else {
        this.selectedTraining(this.trainings[0], 0, true);
      }
    } else if (window.innerWidth <= 768) {
      if (this.$route.params.cardTitleId && trainingIndex != -1) {
        // this.$set(this.articles[articleIndex],'Selected',true);
        this.selectedTraining(this.trainings[trainingIndex], trainingIndex, true);
      } else {
        this.$set(this.trainings[0], "Selected", true);
        this.selectedTraining(this.trainings[0], 0, true);
      }
    }
    let tempString = this.mainColor + "b5";
    this.subColor = tempString;
  }
  get theme() {
    return this.mainColor;
  }
  get subTheme() {
    return this.mainColor + "b5";
    
  }
  get logInStatus() {
    return this.$store.getters["login/isLoggedIn"];
  }
  get user() {
    return this.$store.getters["login/user"];
  }
  get token() {
    return this.$store.getters["login/token"];
  }
  get apiHeader() {
    if (this.logInStatus) {
      return {
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.token,
      };
    }
    return {
      "Content-Type": "application/json",
      Authorization: "Bearer " + "",
    };
  }
  trainings = [
    {
      id: 0,
      status: "",
      sort: "",
      Program: "",
      Is_AWT: "",
      Title: "",
      Card_Brief: "",
      Card_Image: "",
      Title_Image: "",
      Starting_Date: "",
      Training_Duration: 0,
      Training_Link: "",
      Content: "",
      Ratings: {
        id: 0,
        totalRatings: 0,
        userRatings: 0,
      },
      Selected: false,
    },
  ];
  dateM(date: string) {
    let temp = date.toString().split("T")[0];
    let yyyy = temp.split("-")[0];
    let mm = temp.split("-")[1];
    let dd = temp.split("-")[2];
    return dd + "/" + mm + "/" + yyyy;
  }
  calculateRatings(
    ratings: [
      {
        id: number;
        training: number;
        rating_amount: number;
        user_created: string;
      }
    ]
  ) {
    let totalRating = 0;
    let userRating = 0;
    let ratingId = 0;
    for (const id in ratings) {
      totalRating = totalRating + ratings[id].rating_amount;
      if (
        this.user !== null &&
        this.user.id === ratings[id].user_created &&
        ratings[id].training
      ) {
        userRating = ratings[id].rating_amount;
        ratingId = ratings[id].id;
      }
    }
    totalRating = totalRating / ratings.length;
    return {
      id: ratingId,
      totalRatings: totalRating,
      userRatings: userRating,
    };
  }

  async fetchTrainings() {
    let id = this.programId + "";
    await fetch(Api.Platform.GetTrainings.replace("{id}", id), {
      headers: this.apiHeader,
    })
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.trainings = [];
        let results = data.data;
        for (const id in results) {
          let cardImage = results[id].Card_Image;
          let titleImage = results[id].Title_Image;
          let ratings = {
            id: 0,
            totalRatings: 0,
            userRatings: 0,
          };
          if (results[id].Card_Image != null) {
            cardImage = Api.LocalhostImages + "" + results[id].Card_Image;
          }
          if (results[id].Title_Image != null) {
            titleImage = Api.LocalhostImages + "" + results[id].Title_Image;
          }
          if (results[id].Ratings.length != 0) {
            ratings = this.calculateRatings(results[id].Ratings);
          }
          this.trainings.push({
            id: results[id].id,
            status: results[id].status,
            sort: results[id].sort,
            Program: results[id].program,
            Is_AWT: results[id].Is_AWT,
            Title: results[id].Title,
            Card_Brief: results[id].Card_Brief,
            Card_Image: cardImage,
            Title_Image: titleImage,
            Starting_Date: " : " + this.dateM(results[id].Starting_Date),
            Training_Duration: results[id].Training_Duration,
            Training_Link: results[id].Training_Link,
            Content: results[id].Content,
            Ratings: ratings,
            Selected: false,
          });
        }
        if (data != "" && window.innerWidth > 768) {
          this.trainings[0].Selected = true;
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }
  @Watch("$store.getters.trainingIdStatus")
  onTrainingUpdate(
    value: { id: number; value: number },
    oldValue: { id: number; value: number }
  ) {
    console.log("storeWatch");
    var itemIndex = this.trainings.findIndex((item) => item.id === value.id);
    this.trainings[itemIndex].Ratings.userRatings = value.value;
    // this.articles[itemIndex].Claps.push(value);
  }

  @Emit("selecteditem")
  selectedTraining(item: any, index: number, createdCall?: boolean) {
    if (!this.$route.params.cardTitleId && !this.$route.params.cardTitle) {
      this.$router.replace({
        params: {
          cardTitleId: this.trainings[0].id + "",
          cardTitle: this.trainings[0].Title.split(" ").join("-"),
        },
      });
    }
    if (this.selectedIndex !== index || window.innerWidth < 768) {
      this.$route.params.cardTitle =
        this.trainings[index].Title.split(" ").join("-");
      this.$route.params.cardTitleId = this.trainings[index].id + "";
      document.title = this.$route.params.cardTitle;
      if (!createdCall) {
        history.pushState(
          {},
          "",
          `/platform/${this.$route.params.navBarId}/${
            this.$route.params.navBar
          }/${this.$route.params.sideNavBarId}/${
            this.$route.params.sideNavBar
          }/${this.trainings[index].id}/${this.trainings[index].Title.split(
            " "
          ).join("-")}`
        );
      }
      this.$set(this.trainings[this.selectedIndex], "Selected", false);
      this.$set(this.trainings[index], "Selected", true);
      this.selectedIndex = index;
    }
    if (createdCall && window.innerWidth <= 768) {
      return null;
    }
    return item;
  }
}
