




















































































import { Component, Vue, Prop, Emit } from "vue-property-decorator";
import Api from "../../../../Api";

@Component({
  components: {},
})
export default class FaqSideBar extends Vue {
  @Prop() private programId!: number;
  @Prop() private mainColor!: string;
  subColor = ""; 
  selectedIndex = 0;
  faqSections = [
    {
      id: 0,
      status: "",
      sort: "",
      Title: "",
      Description: "",
      Program: "",
      is_Common: "",
      // Content: [{}],
      Content: {
        title: "",
        faq: [{}]
        },
      Selected: false,
    },
  ];

  async created() {
    window.onpopstate = () => {
      this.selectedfaqSection(
        this.faqSections[this.selectedIndex],
        this.selectedIndex,
        true
      );
    };
    await this.fetchFQA();
    const faqIndex = this.faqSections.findIndex(
      (item) => item.id == parseInt(this.$route.params.cardTitleId)
    );
    if (window.innerWidth > 768) {
      if (this.$route.params.cardTitleId && faqIndex != -1) {
        this.selectedfaqSection(this.faqSections[faqIndex], faqIndex, true);
      } else {
        this.selectedfaqSection(this.faqSections[0], 0, true);
      }
    } else if (window.innerWidth <= 768) {
      if (this.$route.params.cardTitleId && faqIndex != -1) {
        // this.$set(this.articles[articleIndex],'Selected',true);
        this.selectedfaqSection(this.faqSections[faqIndex], faqIndex, true);
      } else {
        this.$set(this.faqSections[0], "Selected", true);
        this.selectedfaqSection(this.faqSections[0], 0, true);
      }
    }
    let tempString = this.mainColor + "b5";
    this.subColor = tempString;
  }
  get theme() {
    return this.mainColor;
  }
  get subTheme() {
    return this.mainColor + "b5";
  }

  async fetchFQA() {
    let id = this.programId + "";
    await fetch(Api.Platform.GetFAQ.replace("{id}", id))
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.faqSections = [];
        let results = data.data;
        for (const id in results) {
          let temp = {title:"",faq:[{}]};
          temp = {title:'',faq:[]};
          temp.title = results[id].Title;
          for (const cId in results[id].Content) {
            temp.faq.push({
              id: results[id].Content[cId].id,
              Question: results[id].Content[cId].Question,
              Answer: results[id].Content[cId].Answer,
              Selected: false,
            });
          }
          this.faqSections.push({
            id: results[id].id,
            status: results[id].status,
            sort: results[id].sort,
            Title: results[id].Title,
            Description: results[id].Description,
            Program: results[id].program,
            is_Common: results[id].is_Common,
            Content: temp,
            Selected: false,
          });
        }
        if (data != "" && window.innerWidth > 768) {
          this.faqSections[0].Selected = true;
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }

  @Emit("selecteditem")
  selectedfaqSection(item: any, index: number, createdCall?: boolean) {
    this.$route.params.cardTitle =
      this.faqSections[index].Title.split(" ").join("-");
    this.$route.params.cardTitleId = this.faqSections[index].id + "";
    document.title = this.$route.params.cardTitle;
    if (!createdCall) {
      history.pushState(
        {},
        "",
        `/platform/${this.$route.params.navBarId}/${
          this.$route.params.navBar
        }/${this.$route.params.sideNavBarId}/${this.$route.params.sideNavBar}/${
          this.faqSections[index].id
        }/${this.faqSections[index].Title.split(" ").join("-")}`
      );
    }
    if (this.selectedIndex !== index || window.innerWidth < 768) {
      this.$set(this.faqSections[this.selectedIndex], "Selected", false);
      this.$set(this.faqSections[index], "Selected", true);
      this.selectedIndex = index;
    }
    if (createdCall && window.innerWidth <= 768) {
      return null;
    }
    return item.Content;
  }
}
