import Api from "../../Api";

let timer: number;
export const actions = {
  async login(context: any, payload: any) {
    return context.dispatch("auth", {
      ...payload,
      mode: "login",
    });
  },
  async signup(context: any, payload: any) {
    return context.dispatch("auth", {
      ...payload,
      mode: "signup",
    });
  },
  async auth(context: any, payload: any) {
    const mode = payload.mode;
    let url = Api.Authenticateion.Login;
    let body = JSON.stringify({
      email: payload.email,
      password: payload.password,
    });
    if (mode === "signup") {
      url = Api.Authenticateion.Register;
      body = JSON.stringify({
        email: payload.email,
        password: payload.password,
        role: "95e15d92-ca9a-4b2c-b392-aaaaeeb7fb44",
      });
    }
    const response = await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: body,
    });
    let responseData = await response.json();
    if (response.ok) {
      if (mode === "login") {
        responseData = responseData.data;
        localStorage.setItem("token", responseData.access_token);
      }
    }

    if (!response.ok) {
      const error = new Error(
        responseData.message || "Failed to authenticate. Check your login data."
      );
      throw error;
    }
    if (mode === "login") {
      const userResponse = await fetch(Api.Authenticateion.LoggedUser, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + responseData.access_token,
        },
      });
      let userResponseData = await userResponse.json();
      if (userResponse.ok) {
        userResponseData = userResponseData.data;
      }
      // const expiresIn = +responseData.expiresIn * 1000;
      const expiresIn = +responseData.expires;
      const expirationDate = new Date().getTime() + expiresIn;

      localStorage.setItem("token", responseData.access_token);
      localStorage.setItem("refreshToken", responseData.refresh_token);
      localStorage.setItem("user", JSON.stringify(userResponseData));
      localStorage.setItem("tokenExpiration", expirationDate + "");

      timer = setTimeout(function () {
        context.dispatch("refreshToken", responseData.refresh_token);
      }, expiresIn);

      context.commit("setUserState", {
        loggedIn: true,
        user: userResponseData,
        token: responseData.access_token,
      });
      if (response.ok) {
        location.reload();
      }
    }

    // context.commit('setUser', {
    //   token: responseData.idToken,
    //   userId: responseData.localId
    // });
  },
  tryLogin(context: any) {
    const token = localStorage.getItem("token");
    const user = localStorage.getItem("user");
    let userObject = {};
    if (user !== null && user != "undefined") {
      userObject = JSON.parse(user);
    }
    const refreshToken = localStorage.getItem("refreshToken");
    const tokenExpiration = parseInt(
      localStorage.getItem("tokenExpiration") + ""
    );

    const expiresIn = +tokenExpiration - new Date().getTime();

    // if (expiresIn < 0 || isNaN(expiresIn)) {
    //   return;
    // }
    if (isNaN(expiresIn)) {
      return;
    }

    timer = setTimeout(function () {
      context.dispatch("refreshToken", refreshToken);
    }, expiresIn);

    if (token && user) {
      context.commit("setUserState", {
        loggedIn: true,
        user: userObject,
        token: token,
      });
    }

    // if (token && userId) {
    //   context.commit('setUser', {
    //     token: token,
    //     userId: userId
    //   });
    // }
  },
  logout(context: any) {
    localStorage.removeItem("token");
    localStorage.removeItem("user");
    localStorage.removeItem("refreshToken");
    localStorage.removeItem("tokenExpiration");

    clearTimeout(timer);

    context.commit("setUserState", {
      loggedIn: false,
      user: null,
      token: null,
    });
    location.reload();
    // context.commit('setUser', {
    //   token: null,
    //   userId: null
    // });
  },

  // autoLogout(context:any) {
  //   context.dispatch('logout');
  //   context.commit('setAutoLogout');
  // },

  async refreshToken(context: any, payload: any) {
    const url = Api.Authenticateion.Refresh;
    const response = await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        refresh_token: payload,
        // password: payload.password,
      }),
    });
    let responseData = await response.json();
    responseData = responseData.data;

    if (!response.ok) {
      const error = new Error(
        responseData.message || "Failed to authenticate. Check your login data."
      );
      throw error;
    }
    const expiresIn = +responseData.expires;
    const expirationDate = new Date().getTime() + expiresIn;

    localStorage.setItem("token", responseData.access_token);
    localStorage.setItem("refreshToken", responseData.refresh_token);
    localStorage.setItem("tokenExpiration", expirationDate + "");

    timer = setTimeout(function () {
      context.dispatch("refreshToken");
    }, expiresIn);

    context.commit("setUserState", {
      token: responseData.access_token,
    });

    // context.commit('setUserState', {
    //   loggedIn: true,
    // })
  },
  async updateUser(context: any, payload: number) {
    const token = localStorage.getItem("token");
    const user = localStorage.getItem("user");
    let userObject: any;
    // console.log(payload)
    if (user !== null) {
      userObject = JSON.parse(user);
      if (userObject.Programs == null) {
        userObject.Programs = [];
      }
      userObject.Programs.push(payload);
    }
    const userResponse = await fetch(
      Api.Authenticateion.UpdateUser.replace("{id}", userObject.id),
      {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify({
          email: userObject.email,
          Programs: userObject.Programs,
        }),
      }
    );
    let userResponseData = await userResponse.json();
    if (userResponse.ok) {
      userResponseData = userResponseData.data;
      localStorage.setItem("user", JSON.stringify(userResponseData));
      context.commit("setUserState", {
        loggedIn: true,
        user: userResponseData,
        token: token,
      });
    }
  },
};
