























































import { Component, Vue, Prop } from "vue-property-decorator";

@Component({
  components: {},
})
export default class FaqContentComponent extends Vue {
  @Prop() private content!: any;
  @Prop() private mainColor!: string;
  selectedIndex = 0;
  items: [
    {
      id: number;
      Question: string;
      Answer: string;
      Selected: false;
    }
  ] = this.content;
  
  get selectedFAQ() {
    return this.content;
  }
  get theme() {
    return this.mainColor;
  }
  get backgroundTheme() {
    return this.mainColor + "b8";
  }
  toggleArrow(itemID: number) {
    this.selectedIndex = itemID;
    this.$set(this.content[itemID], "Selected", !this.content[itemID].Selected);
  }
}
