





















































































































































































































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import BaseDialog from "../components/UI/BaseDialog.vue";
import Login from "../views/Authentication/Login.vue";
import RegisterUser from "./User/RegisterUser.vue";

@Component({
  components: {
    BaseDialog,
    Login,
    RegisterUser,
  },
})
export default class NavBar extends Vue {
  @Prop() private color!: string;
  navBarColor = this.color;
  login = false;
  popUpMode = true;
  get navColor() {
    return this.color;
  }
  get logInStatus() {
    return this.$store.getters["login/isLoggedIn"];
  }
  get loginPage() {
    return this.login;
  }
  revertLoginPageStatus() {
    if (!this.popUpMode) {
      this.popUpMode = !this.popUpMode;
    }
    this.login = !this.login;
  }
  logOut() {
    this.$store.dispatch("login/logout");
    if (this.$route.name === "profile") {
      this.$router.push({ name: "platform" });
    }
  }
  @Watch("$store.getters.loginPopStatus")
  setLoginPopUp(newValue: boolean, oldValue: boolean) {
    this.revertLoginPageStatus();
  }
}
