



















































































import { Component, Vue, Prop, Emit } from "vue-property-decorator";
import Api from "../../../../Api";

@Component({
  components: {},
})
export default class ResourcesSideBar extends Vue {
  @Prop() private programId!: number;
  @Prop() private mainColor!: string;
  subColor = "";
  selectedIndex = 0;

  async created() {
    window.onpopstate = () => {
      this.selectedResource(
        this.resources[this.selectedIndex],
        this.selectedIndex,
        true
      );
    };
    await this.fetchResources();
    const resourceIndex = this.resources.findIndex(
      (item) => item.id == parseInt(this.$route.params.cardTitleId)
    );
    if (window.innerWidth > 768) {
      if (this.$route.params.cardTitleId && resourceIndex != -1) {
        this.selectedResource(this.resources[resourceIndex], resourceIndex, true);
      } else {
        this.selectedResource(this.resources[0], 0, true);
      }
    } else if (window.innerWidth <= 768) {
      if (this.$route.params.cardTitleId && resourceIndex != -1) {
        // this.$set(this.articles[articleIndex],'Selected',true);
        this.selectedResource(this.resources[resourceIndex], resourceIndex, true);
      } else {
        this.$set(this.resources[0], "Selected", true);
        this.selectedResource(this.resources[0], 0, true);
      }
    }
    let tempString = this.mainColor + "b5";
    this.subColor = tempString;
  }
  get theme() {
    return this.mainColor;
  }
  get subTheme() {
    return this.mainColor + "b5";
  }
  titleImage(type: string, images?: boolean) {
    if (type === "Video") {
      return require("../../../../assets/img/icons/video-marketing.png");
    } else if (type === "Audio") {
      return require("../../../../assets/img/icons/headphones.png");
    } else if (type === "Pdf") {
      return require("../../../../assets/img/icons/pdf-file.png");
    } else if (type === "Link") {
      return require("../../../../assets/img/icons/foreign.png");
    } else if (type === "Images" || images === true) {
      return require("../../../../assets/img/icons/image-gallery.png");
    }
    return null;
  }
  resources = [
    {
      id: 0,
      status: "",
      sort: "",
      Program: "",
      Is_AWT: "",
      Title: "",
      Card_Brief: "",
      Card_Image: "",
      Content: "",
      Have_Images: true,
      Files: [{}],
      Files_Type: "",
      File: "",
      Link: "",
      Selected: false,
    },
  ];

  async fetchResources() {
    let id = this.programId + "";
    await fetch(Api.Platform.GetResources.replace("{id}", id))
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.resources = [];
        let results = data.data;
        for (const id in results) {
          let imageSource = results[id].Card_Image;
          if (results[id].Card_Image != null) {
            imageSource = Api.LocalhostImages + "" + results[id].Card_Image;
          }
          this.resources.push({
            id: results[id].id,
            status: results[id].status,
            sort: results[id].sort,
            Program: results[id].program,
            Is_AWT: results[id].Is_AWT,
            Title: results[id].Title,
            Card_Brief: results[id].Card_Brief,
            Card_Image: imageSource,
            Content: results[id].Content,
            Have_Images: results[id].Have_Images,
            Files: results[id].Files,
            Files_Type: results[id].Files_Type,
            File: results[id].File,
            Link: results[id].Link,
            Selected: false,
          });
        }
        if (data != "" && window.innerWidth > 768) {
          this.resources[0].Selected = true;
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }

  @Emit("selecteditem")
  selectedResource(item: any, index: number, createdCall?: boolean) {
    if (!this.$route.params.cardTitleId && !this.$route.params.cardTitle) {
      this.$router.replace({
        params: {
          cardTitleId: this.resources[0].id + "",
          cardTitle: this.resources[0].Title.split(" ").join("-"),
        },
      });
    }
    if (this.selectedIndex !== index || window.innerWidth < 768) {
      this.$route.params.cardTitle =
        this.resources[index].Title.split(" ").join("-");
      this.$route.params.cardTitleId = this.resources[index].id + "";
      document.title = this.$route.params.cardTitle;
      if (!createdCall) {
        history.pushState(
          {},
          "",
          `/platform/${this.$route.params.navBarId}/${
            this.$route.params.navBar
          }/${this.$route.params.sideNavBarId}/${
            this.$route.params.sideNavBar
          }/${this.resources[index].id}/${this.resources[index].Title.split(
            " "
          ).join("-")}`
        );
      }
      this.$set(this.resources[this.selectedIndex], "Selected", false);
      this.$set(this.resources[index], "Selected", true);
      this.selectedIndex = index;
    }
    if (createdCall && window.innerWidth <= 768) {
      return null;
    }
    return item;
  }
}
