








































































import { Component, Vue, Prop } from "vue-property-decorator";
import Comments from "../../../components/Platform/Comments.vue";
import Rating from "../../../components/Platform/Rating.vue";

@Component({
  components: {
    Comments,
    Rating,
  },
})
export default class TrainingContentComponent extends Vue {
  @Prop() private content!: any;
  @Prop() private mainColor!: string;
  stars = 3.65;
  
  get selectedTraining() {
    return this.content;
  }
  get theme() {
    return this.mainColor;
  }
  get backgroundTheme() {
    return this.mainColor + "b8";
  }
  get logInStatus() {
    return this.$store.getters["login/isLoggedIn"];
  }
  // created(){
  //     alert(this.selectedTraining.Ratings.id)
  // }
  goToTraining() {
    if (this.selectedTraining.Training_Link != null) {
      window.open(this.selectedTraining.Training_Link);
    }
  }
}




